import { getDbConnection } from "@/lib/db";
import { generateQuestions } from "@/lib/geminiai";
import { notFound } from "next/navigation";
import { ArrowLeft, Check, X } from "lucide-react";
import Link from "next/link";
import { Button } from "@/components/ui/button";

interface QuizPageProps {
  params: { id: string };
}

export default async function QuizPage({ params }: QuizPageProps) {
  const sql = await getDbConnection();
  const [summary] = await sql`
    SELECT summary_text, title FROM pdf_summaries WHERE id = ${params.id}
  `;

  if (!summary) {
    notFound();
  }

  const rawQuestions = await generateQuestions(summary.summary_text);
  
  // Process questions to detect type (MCQ or open-ended)
  const questions = rawQuestions.map((q: any) => {
    const isMCQ = q.options?.length === 4;
  
    return {
      text: q.question,
      isMCQ,
      options: q.options || [],
      answer: q.answer,
      selected: "",
      revealed: false
    };
  });

  return (
    <div className="max-w-3xl mx-auto py-8 px-4">
      <div className="mb-6">
        <Button asChild variant="ghost" className="pl-0">
          <Link href={`/summaries/${params.id}`} className="flex items-center">
            <ArrowLeft className="w-4 h-4 mr-2" />
            Back to Summary
          </Link>
        </Button>
        <h1 className="text-3xl font-bold mt-4">Test Your Knowledge</h1>
        <p className="text-muted-foreground mt-2">
          Quiz for: <span className="font-medium text-primary">{summary.title}</span>
        </p>
      </div>

      {questions.length === 0 ? (
        <div className="text-center py-12">
          <p className="text-lg text-muted-foreground">No questions generated.</p>
          <Button variant="outline" className="mt-4">
            Try Again
          </Button>
        </div>
      ) : (
        <div className="space-y-6">
          {questions.map((q, index) => (
            <div key={index} className="border rounded-lg overflow-hidden bg-card">
              <div className="p-6">
                <div className="flex items-start">
                  <div className="flex items-center justify-center bg-primary/10 text-primary rounded-full w-8 h-8 mr-4">
                    {index + 1}
                  </div>
                  <h2 className="text-lg font-semibold">{q.text.split('?')[0]}?</h2>
                </div>

                {q.isMCQ ? (
                  <div className="mt-4 space-y-3 ml-12">
                    {q.options?.map((opt, i) => (
                      <div key={i} className="flex items-center">
                        <input
                          type="radio"
                          id={`q${index}-opt${i}`}
                          name={`q${index}`}
                          className="peer hidden"
                        />
                        <label
                          htmlFor={`q${index}-opt${i}`}
                          className="flex-1 p-3 border rounded-lg cursor-pointer peer-checked:border-primary peer-checked:bg-primary/5 transition-colors"
                        >
                          <span className="font-medium text-primary mr-2">
                            {String.fromCharCode(97 + i)})
                          </span>
                          {opt}
                        </label>
                      </div>
                    ))}
                  </div>
                ) : (
                  <div className="mt-4 ml-12">
                    <textarea
                      placeholder="Type your answer here..."
                      className="w-full p-3 border rounded-lg focus:ring-2 focus:ring-primary/50 focus:border-primary"
                      rows={3}
                    />
                  </div>
                )}

                <div className="mt-4 flex justify-end space-x-3 ml-12">
                  <Button variant="outline" size="sm">
                    <Check className="w-4 h-4 mr-2 text-green-500" />
                    Check Answer
                  </Button>
                  <Button variant="outline" size="sm">
                    <X className="w-4 h-4 mr-2 text-red-500" />
                    Reveal Answer
                  </Button>
                </div>
              </div>
            </div>
          ))}

          <div className="flex justify-between mt-8">
            <Button variant="outline">Previous</Button>
            <Button>Submit Quiz</Button>
          </div>
        </div>
      )}
    </div>
  );
}